import React, { useMemo, useState, useEffect } from "react";
import classnames from "classnames";

require("./style.scss");

type IRadio = {
  onChange: Function;
  option: {
    label: string;
    value: string | number;
  };
  index: number;
  disabled: boolean;
  checked: boolean;
};

const Radio = (props: IRadio) => {
  const { value, label } = props.option;
  const onChange = (e: any) => {
    if (props.disabled) return;
    // console.log(e);
    props.onChange(value);
  };

  return useMemo(
    () => (
      <>
        <label
          className={classnames("ant-radio-button-wrapper", {
            "ant-radio-button-wrapper-checked": props.checked,
            "ant-radio-button-wrapper-disabled": props.disabled
          })}
          onClick={onChange}
        >
          <span
            className={classnames("ant-radio-button", {
              "ant-radio-button-checked": props.checked,
              "ant-radio-button-disabled": props.disabled
            })}
          >
            <input
              type="radio"
              className="ant-radio-button-input"
              value={value}
              checked={props.checked}
              disabled={props.disabled}
              onChange={() => {}}
            />
            <span className="ant-radio-button-inner"></span>
          </span>
          <span>{label}</span>
        </label>
      </>
    ),
    [label, value, props.checked, props.disabled]
  );
};

export default Radio;
