require("./style.scss");
import React from "react";
import { Icon, InputItem } from "antd-mobile";
import { observer } from "mobx-react";
import RadioGroup from "@/components/RadioGroup";
import { useHistory } from "react-router-dom";
type IPropsItem = {
  model: IAutoItem;
  index: number;
  onChange: Function;
  disabled: boolean;
  startTime: string;
  endTime: string;
  userId: string;
};
type IAutoItem = {
  id: string;
  name: string;
  result: string;
  remark: string;
  dotype: string;
};

type IState = {
  result: string;
  remark: string;
};

const AutoItem = observer((props: IPropsItem) => {
  const history = useHistory();
  const onRadioChange = (val: any) => {
    props.onChange({ result: val }, props.index);
  };

  const onInputChange = (val: string) => {
    props.onChange( 'remark' ,val , props.index);
  };

  const onDetail = () => {
    const { dotype } = props.model;
    const { startTime, endTime, userId } = props;
    const state = {
      dotype,
      startTime,
      endTime,
      userId,
    };
    history.push({
      pathname: "/perform_detail_view",
      state,
    });
  };

  const { name, remark } = props.model
  let{result}=props.model;
  switch (result){
   case '未检查': result='2';break;
   case '已检查':result='0';break;
   default :result='1';break;
  }
  return (
    <>
      <div className="autoItem">
        <div className="autoItem_title">{name}</div>
        <RadioGroup
          className="autoItem_radio"
          onChange={onRadioChange}
          value={result}
          disabled={true}
          options={[
            { label: "已检查", value: "0" || "1" },
            { label: "未检查", value: "2" },
          ]}
        ></RadioGroup>
        <div className="autoItem_btn">
          <InputItem
            className="autoItem_input"
            disabled={props.disabled}
            value={remark}
            onChange={onInputChange}
            maxLength={50}
          />
          <div className="autoItem_detail" onClick={onDetail}>
            履职详情
          </div>
        </div>
      </div>
    </>
  );
});

export default AutoItem;
