import React, { useEffect } from "react";
require("./style.scss");
import { Icon, List, Tabs } from "antd-mobile";
import { observer, inject } from "mobx-react";
import EasyNarBar from "@/components/EasyNarBar/EasyNarBar";
import AutoItems from "./AutoItems";
import ManualItems from "./ManualItems";
import DetailTop from "./DetailTop";
import DetailSubmit from "./DetailSubmit";
import { StickyContainer, Sticky } from "react-sticky";
import { useHistory, useLocation } from "react-router-dom";
import getSearchParams from "@/utils/tools/getSearchParams";
const PerformDetail = observer((props: any) => {
  const history = useHistory();
  const location = useLocation();
  let { status } = props.store;
  status = status ? status : "0";
  const { updatePics, initStore, startTime, endTime, userId, onTextareaChange, clearData } = props.store;
  useEffect(() => {
    const { state }: any = location;
    if (state) {
      switch (state.pathname) {
        case "/select-image":
          updatePics(state);
          break;
        default:
          initStore(state);
      }
    } else {
      let { id } = getSearchParams(location.search);
      id = id.substring(0, id.indexOf("?"));
      const state2 = {
        TabIndex: "0",
        id,
        status: "0"
      };
      initStore(state2);
    }
    // clearData();
  }, []);

  const onLeftClick = () => {
    const { state }: any = location;
    onTextareaChange("");
    history.push({
      pathname: "/performance_management",
      state: {
        TabIndex: state ? state.TabIndex : "0"
      }
    });
    // history.goBack();
  };
  const renderTabBar = (props: any) => {
    return (
      <Sticky>
        {({ style }: any) => (
          <div style={{ ...style, zIndex: 1 }}>
            <Tabs.DefaultTabBar {...props} />
          </div>
        )}
      </Sticky>
    );
  };
  return (
    <div>
      <EasyNarBar mainContent="履职详情" leftContent={<Icon type="left" />} onLeftClick={onLeftClick}></EasyNarBar>
      <List
        style={{
          height: document.documentElement.clientHeight - 60,
          overflow: "auto"
        }}
      >
        <DetailTop model={props.store} history={history} status={status} />
        {status !== "2" ? (
          <>
            <div className="perform_title">
              <span>履职任务列表</span>
            </div>
            <StickyContainer>
              <Tabs
                tabs={[
                  { title: "手动填报", sub: "0" },
                  { title: "自动填报", sub: "1" }
                ]}
                initialPage={0}
                renderTabBar={renderTabBar}
                swipeable={false}
              >
                <ManualItems model={props.store} disabled={status === "1"} />
                <AutoItems model={props.store} disabled={status === "1"} startTime={startTime} endTime={endTime} userId={userId} />
              </Tabs>
            </StickyContainer>
          </>
        ) : null}
        {status != 2 ? <DetailSubmit model={props.store} status={status} /> : ""}
      </List>
    </div>
  );
});

export default inject((stores: any) => ({
  store: stores.performDetailStore
}))(PerformDetail);
