import React from "react";
import ManualItem from "./item";
import { observer } from "mobx-react";
import { useHistory } from "react-router-dom";

type IProps = {
  model: any;
  disabled: boolean;
  history: any;
};

type IManualItem = {
  id: string;
  name: string;
  result: string;
  pics: Array<string>;
  remark: string;
};

const ManualItems = observer((props: any) => {
  const { manualItems, onManualItemsChange } = props.model;
  return (
    <>
      {manualItems.map((item: IManualItem, index: number) => (
        <ManualItem key={item.id} index={index} model={item} onChange={onManualItemsChange} disabled={props.disabled} />
      ))}
    </>
  );
});

export default ManualItems;
