import React from "react";
import { Icon, InputItem, List } from "antd-mobile";
import RadioGroup from "@/components/RadioGroup";
import { observer } from "mobx-react";
import { toJS } from "mobx";
import { useHistory } from "react-router-dom";
require("./style.scss");
type IPropsItem = {
  model: IManualItem;
  index: number;
  onChange: Function;
  disabled: boolean;
};

type IManualItem = {
  id: string;
  name: string;
  result: string;
  pics: Array<string>;
  remark: string;
};

type IState = {
  result: string;
  remark: string;
};

const ManualItem = observer((props: IPropsItem) => {
  const history = useHistory();
  const { name, remark, pics } = props.model;
  const { result } = props.model;
  const length = pics.length;
  const len = !props.disabled ? pics && pics.filter((item: any) => item.operation === "ADD").length : length;
  const onRadioChange = (value: any) => {
    props.onChange("result", value, props.index);
  };

  const onInputChange = (val: string) => {
    props.onChange("remark", val, props.index);
  };

  const addPicHandle = () => {
    const picList =
      pics &&
      pics.map((item: any) => {
        if (!item.operation) {
          return { ...item, operation: "ADD", uploadSource: "user" };
        }
        return item;
      });
    const state = {
      pathname: "/perform_detail",
      index: props.index,
      disabled: props.disabled,
      pics: toJS(picList, { recurseEverything: true })
    };

    history.push({ pathname: "/select-image", state });
  };

  return (
    <>
      <div className="manualitem">
        <div className="manualitem_title">{props.index + 1 + " 、 " + name}</div>
        <RadioGroup
          className="manualitem_radio"
          onChange={onRadioChange}
          value={result}
          disabled={props.disabled}
          options={[
            { label: "正常", value: "0" },
            { label: "有问题", value: "1" },
            { label: "未检查", value: "2" }
          ]}
        ></RadioGroup>
        <div className="manualitem_btn">
          <InputItem className="manualitem_input" disabled={props.disabled} value={remark} onChange={onInputChange} maxLength={50} />
          <div className="manualitem_addpic" onClick={addPicHandle}>
            {len ? `${len}张图片` : !props.disabled ? "添加图片" : ""}
          </div>
        </div>
      </div>
    </>
  );
});

export default ManualItem;
