import React from "react";
import AutoItem from "./item";
import { observer } from "mobx-react";

type IProps = {
  model: any;
  disabled: boolean;
  startTime: string;
  endTime: string;
  userId: string;
};

type IAutoItem = {
  id: string;
  name: string;
  result: string;
  remark: string;
  dotype: string;
};

const AutoItems = observer((props: any) => {
  const { autoItems, onAutoItemsChange } = props.model;
  return (
    <>
      {autoItems.map((item: IAutoItem, index: number) => (
        <AutoItem
          key={item.id}
          index={index}
          model={item}
          startTime={props.startTime}
          endTime={props.endTime}
          userId={props.userId}
          onChange={onAutoItemsChange}
          disabled={props.disabled}
        />
      ))}
    </>
  );
});

export default AutoItems;
