import React, { useMemo, useEffect, useState } from "react";
require("./style.scss");
import Radio from "./item";
import classnames from "classnames";
type IRadioGroup = {
  options: Array<{ label: string; value: string | number }>;
  onChange: Function;
  disabled?: boolean;
  value: string | number;
  className?: string;
};

const RadioGroup = (props: IRadioGroup) => {
  const [checkVal, setCheckVal] = useState(props.value);
  const onChange = (value: string) => {
    if (props.disabled) return;
    props.onChange(value);
    setCheckVal(value);
  };

  useEffect(() => {
    setCheckVal(props.value);
  }, [props.value]);

  const { options, disabled } = props;
  return useMemo(
    () => (
      <>
        <div className={classnames("ant-radio-group ant-radio-group-solid", props.className || "")}>
          {options.map((item, index) => (
            <Radio key={index} index={index} onChange={onChange} option={item} checked={item.value == checkVal} disabled={disabled} />
          ))}
        </div>
      </>
    ),
    [options, checkVal]
  );
};

export default RadioGroup;
