import React from "react";
import VList from "@/components/VList";
import { getWxApi } from "@/service/wx-api";
import { toJS } from "mobx";
import storage from "@/utils/storage";
import { Toast } from "antd-mobile";
require("./style.scss");
type IPropsDetailTop = {
  history: any;
  status: any;
  model: {
    executeName: string;
    startTime: string;
    orgName: string;
    memberName: string;
    workingBasisName: string;
    workingBasisAttas: any;
    endTime: string;
    handleTime: string;
  };
};

export default class DetailTop extends React.Component<IPropsDetailTop> {
  api: any;
  getWxApi: Function;
  phoneType: string;
  constructor(props: IPropsDetailTop) {
    super(props);
    this.api = getWxApi(location.href);
    this.phoneType = navigator.userAgent;
  }
  perviewFile = () => {
    const { model } = this.props;
    const { fileType, filepath, fileSize, filename } = model["workingBasisAttas"][0];
    // console.log('fileType',fileType,'filepath',filepath,'size',fileSize,'filename',filename)

    let url;
    switch (fileType) {
      case 0:
        url = `${window.location.origin}/file/formAtt/${filepath}`;
        break;
      case 1:
        url = `${window.location.origin}/api/restApi/pageControl/attachment/${filepath}`;
        break;
      default:
    }
    //if (!(this.phoneType.indexOf("Android") > -1 || this.phoneType.indexOf("Linux") > -1)) {
    const tokenId = storage.getItem("tokenId");
    const userId = storage.getItem("userId");
    const client_token = storage.getItem("client_token");
    url = `${url}?tokenId=${tokenId}&userId=${userId}&client_token=${client_token}`;
    //}
    this.api.media.previewFile.previewFile({
      url: url,
      name: filename,
      size: fileSize,
      fail: () => {
        Toast.fail("当前的企业微信版本不支持预览功能");
      }
    });
  };
  render() {
    const {
      model,
      model: { workingBasisAttas },
      status
    } = this.props;
    const modelName =
      status == 2
        ? {
            executeName: "任务名称",
            startTime: "履职任务日期",
            endTime: "履职任务完成时限",
            orgName: "履职单位",
            memberName: "履职人员"
          }
        : status != 1
        ? {
            executeName: "任务名称",
            startTime: "履职任务日期",
            orgName: "履职单位",
            memberName: "履职人员"
          }
        : {
            executeName: "任务名称",
            startTime: "履职任务日期",
            handleTime: "履职日期",
            orgName: "履职单位",
            memberName: "履职人员"
          };
    // let url;
    // let fname = model["workingBasisName"];
    // if (workingBasisAttas.length > 0) {
    //   const { fileType, filepath, filename } = workingBasisAttas[0];
    //   fname = filename;
    //   switch (fileType) {
    //     case 0:
    //       url = `${window.location.origin}/file/formAtt/${filepath}`;
    //       break;
    //     case 1:
    //       url = `${window.location.origin}/api/restApi/pageControl/attachment/${filepath}`;
    //       break;
    //     default:
    //   }
    // }
    // console.log("文件地址url", url);
    const list = Object.keys(modelName).map(item => ({
      label: modelName[item],
      value: model[item]
    }));
    return (
      <>
        <VList list={list} />

        <div className="VlistItem">
          <div className="VlistItem_label" style={{ flex: 4 }}>
            履职工作依据
          </div>
          {/*onClick={this.perviewFile*/}
          {/* {model["workingBasisName"]} */}
          {/* {this.phoneType.indexOf("Android") > -1 || this.phoneType.indexOf("Linux") > -1 ? ( */}
          <div className="VlistItem_value" style={{ flex: 6, color: "#108ee9" }} onClick={this.perviewFile}>
            {model["workingBasisName"]}
          </div>
        </div>
      </>
    );
  }
}
