require("./style.scss");
import React, { useState } from "react";
import { TextareaItem, Button, WingBlank, Toast, List } from "antd-mobile";
import { observer } from "mobx-react";
import { useHistory } from "react-router-dom";
type IPropsDetailSubmit = {
  model: any;
  status: number;
};

const DetailSubmit = observer((props: IPropsDetailSubmit) => {
  const history = useHistory();

  const { remark, onTextareaChange, sumbited } = props.model;
  const { status } = props;
  const onSubmit = () => {
    props.model.onSubmit(() => {
      history.push({ pathname: "/performance_management" });
      Toast.success("保存成功");
    });
  };

  return (
    <div className="perform_detail_submit">
      <TextareaItem
        className="perform_detail_textareaItem"
        placeholder="请输入检查情况说明"
        autoHeight
        value={remark}
        labelNumber={5}
        onChange={onTextareaChange}
        disabled={status != 0}
      />

      {status == 0 ? (
        <WingBlank>
          <Button type="primary" onClick={onSubmit} disabled={sumbited}>
            提交
          </Button>
        </WingBlank>
      ) : (
        ""
      )}
    </div>
  );
});

export default DetailSubmit;
