import React from "react";

import VListItem from "./item";

require("./style.scss");
type IVListItem = {
  list: Array<any>;
  topBorder?: boolean;
  bottomBorder?: boolean;
  noBorder?: boolean;
  scale?: Array<number>;
};

export default class VList extends React.PureComponent<IVListItem> {
componentDidMount(){
}

  render() {
    const { scale, list, noBorder, bottomBorder, topBorder } = this.props;
    return (
      <>
        {list?list.map((item, index) => (
          <VListItem
            key={index}
            label={item.label}
            value={item.value}
            {...{ scale, noBorder, bottomBorder, topBorder }}
          />
        )):'该用户没有这个月的履职统计数据'}
      </>
    );
  }
}
