import React from "react";
import classnames from "classnames";
require("./style.scss");
type IVListItem = {
  label: string;
  value: string;
  topBorder?: boolean;
  bottomBorder?: boolean;
  scale?: Array<number>;
};

export default class VListItem extends React.Component<IVListItem> {
  render() {
    const { topBorder, bottomBorder, scale, label, value } = this.props;

    return (
      <>
        <div
          className={classnames({
            VlistItem: true,
            topBorder: topBorder || false,
            bottomBorder: bottomBorder === false ? false : true
          })}
        >
          <div className="VlistItem_label" style={{ flex: (scale && scale[3]) || 4 }}>
            {label}
          </div>
          <div className="VlistItem_value" style={{ flex: (scale && scale[5]) || 6 }}>
            {value}
          </div>
        </div>
      </>
    );
  }
}
